body{
  font: 15px/1.5 Arial, Helvetica,sans-serif;
  padding:0;
  margin:0;
  background-color:#0f4c75;
  overflow: auto;
}

/* Global */
.container{
  width:100%;
  margin:auto;
  overflow:hidden;
  display: fixed;
  position: relative;


}


ul{
  margin:0;
  padding:0;
}

.button_1{
  height:38px;
  background:#e8491d;
  border:0;
  padding-left: 20px;
  padding-right:20px;
  color:#ffffff;
}


/* Header **/
header{
  background:#07689f;
  color:#f7f7ee;
  min-height:70px;
  border-bottom:#f1f3f8 3px solid;

}




header #branding h1{
  text-align: center;
  font-family: 'Roboto Mono', monospace;
  font-size: 75px;

}



header .highlight, header .current a{
  color:#e8491d;
  font-weight:bold;
}

header a:hover{
  color:#cccccc;
  font-weight:bold;
}

/* Showcase */
#showcase{
  min-height:400px;
  margin-left: auto;
  margin-right: auto;
  text-align:center;
  height: 700px;
  font-family: 'Roboto Mono', monospace;

}

.headingtext{
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0, 0.4); /* Black w/opacity/see-through */
  color: white;
  font-weight: bold;
  border: 3px solid #f1f1f1;
  position: absolute;
  top: 79%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  width: 80%;
  height: 500px;
  text-align: center;
  position: absolute;
  top: 560px;
  margin-bottom: 0;
  display: flex;
  flex-wrap: wrap;
  margin:auto;
  vertical-align: top;


}


.profilepic{
  width: 550px;
  height: 500px;
  position: relative;
  left: 680px;
  max-width: 100%;







}


.portfoliotext{
  width: 500px;
  height: 500px;
  font-size: 30px;
  position: absolute;
  left: 75px;
    flex-shrink:3;





}


#smallerprofile{
  display: none;
}


#largerprofile{
  min-width: 600px;


}

.abouttitle{
  margin-bottom: 0;
}

.abouttext{
  line-height: 40px;
  text-align: center;


}

.imgcontainer{
  background:url('./images/tree.jpg') no-repeat;
  filter: opacity(50%);
  height: 700px;
  border-bottom: 3px solid #f1f1f1;
  position: relative;
  background-size: 100% 100%;

}


#showcase h1{
  margin-top:100px;
  font-size:55px;
  margin-bottom:10px;
}

#showcase p{
  font-size:20px;
}








footer{
  padding:20px;
  margin-top:20px;
  color:#ffffff;
  background-color:#07689f;
  text-align: center;
  border-top: 3px solid #f1f1f1;
  font-family: 'Roboto Mono', monospace;
}

.footertext{
  display: inline-block;
}






