.view-first a.info {
  opacity: 0;
  transition: all 0.2s ease-in-out;
}


.view-first:hover img {
  transform: scale(1.1);
}

.view-first:hover .mask {
  opacity: 1;
}

.view-first:hover h2,
.view-first:hover p,
.view-first:hover a.info {
  opacity: 1;
  transform: translateY(0px);
}

.view-first:hover p {
  transition-delay: 0.1s;
}

.view-first:hover a.info {
  transition-delay: 0.2s;
}


 


.view:hover {
  box-shadow: 0 0 5px #000;
  cursor: pointer;



}







