@media(max-width: 1450px){


  .profilepic{
    width: 460px;
  }
}



@media(max-width: 1400px){


  .profilepic{
    width: 420px;
  }
}




/* Media Queries */
@media(max-width: 1268px){
  header #branding,
  header nav,
  header nav li,
  #newsletter h1,
  #newsletter form,
  #boxes .box,
  .headtitleprojects,
  article#main-col,
  aside#sidebar{
    float:none;
    text-align:center;
    width:100%;
  }

  header{
    padding-bottom:20px;
  }

  #showcase h1{
    margin-top:40px;
  }

  #newsletter button, .quote button{
    display:block;
    width:100%;
  }

  #newsletter form input[type="email"], .quote input, .quote textarea{
    width:100%;
    margin-bottom:5px;
  }




.profilepic{
  width: 400px;
  height: 250px;
  float: right;
